import { mapState } from 'vuex'
export default {
    computed:{
        ...mapState(['systemInfo'])
    },
    props:{
        // 是否固定在顶部，默认固定
        hasFixed:{
            type: [String,Boolean],
            default: true
        },
        // 是否显示状态栏，默认显示
        hasStatus:{
            type: [String,Boolean],
            default: true
        },
        // 是否显示标题栏，默认不显示
        hasTitle:{
            type: [String,Boolean],
            default: false
        },
        // 标题的背景颜色
        backgroundColor:{
            type: String,
            default: '#010101'
        },
        // container 
        opcity:{
            type: [String,Number],
            default: 0
        },
        // 自定义导航栏的高度，默认44px
        titleHeight:{
            type: [String,Number],
            default: '44px'
        },
        //左侧图标
        leftIcon: {
			type: [String,Array],
			default: ''
        },
        //标题
        contentTitle: {
            type: String,
			default: ''
        },
        //右侧图标
        rightIcon:{
            type: [String,Array],
            default: ''
        }
    },
    methods:{
        // 左侧点击事件
        onClickLeft(item){
            this.$emit(item)
        },
        // 右侧点击事件
        onClickRight(item){
            this.$emit(item)
        }
    }
}
